<template>
    <b-form class="main-container">
        <h4>Links</h4>
        <b-row>
            <b-col md="6">
                <div class="link-point">
                    <span>Start point </span>
                    <i class="fa fa-map-marker"></i>
                </div>
                <b-form-group label="Location-A" label-for="selectLocationA">
                    <b-select
                        v-model="$v.form[0].location.$model"
                        id="selectLocationA"
                        :options="locations"
                        :class="{ 'is-invalid': $v.form[0].location.$error }"
                        @change="onLocationChange(0)"
                    ></b-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Device" label-for="selectDeviceA">
                    <b-select
                        v-model="$v.form[0].device.$model"
                        id="selectDeviceA"
                        :options="devices[0]"
                        :class="{ 'is-invalid': $v.form[0].device.$error }"
                    ></b-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
            </b-col>

            <b-col md="6">
                <div class="link-point">
                    <span>End point </span>
                    <i class="fa fa-map-marker"></i>
                </div>
                <b-form-group label="Location-B" label-for="selectLocationB">
                    <b-select
                        v-model="$v.form[1].location.$model"
                        id="selectLocationB"
                        :options="locations"
                        :class="{ 'is-invalid': $v.form[1].location.$error }"
                        @change="onLocationChange(1)"
                    ></b-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Device" label-for="selectDeviceB">
                    <b-select
                        v-model="$v.form[1].device.$model"
                        id="selectDeviceB"
                        :options="devices[1]"
                        :class="{ 'is-invalid': $v.form[1].device.$error }"
                    ></b-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
            </b-col>
        </b-row>
    </b-form>
</template>

<script>
import { required } from 'vuelidate/lib/validators';
import Backend from '../services/backend';
import Preloader from './Preloader';
import StringMask from 'string-mask';
const backend = new Backend();

export default {
    name: 'CircuitLinksForm',
    components: {
        Preloader
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            locations: [],
            devices: [
                [], []
            ],
            oldLinks: [],
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id',
        'submit'
    ],
    validations: {
        form: {
            0: {
                location: { },
                device: {}
            },
            1: {
                location: {},
                device: {}
            }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.clearForm();
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/circuit_links/?circuit=' + id)
                    .then((response) => {
                        let links = response.data.results;
                        this.oldLinks = response.data.results;
                        if (links.length === 0) {
                            this.clearForm();
                            return;
                        }
                        this.form[0].device = links[0].device;
                        this.form[0].location = links[0].location;
                        this.loadDevices(0);

                        if (links[1]) {
                            this.form[1].device = links[1].device;
                            this.form[1].location = links[1].location;
                            this.loadDevices(1);
                        }
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
            this.loadLocations();
        },
        onSubmit(circuitId) {
            let link1 = {
                circuit: circuitId,
                location: this.form[0].location,
                device: this.form[0].device
            };
            let link2 = {
                circuit: circuitId,
                location: this.form[1].location,
                device: this.form[1].device
            };
            this.isSaving = true;
            if (this._props.pageType === 'add') {
                let requests = [];
                if (link1) {
                    requests.push(backend.postRequest('services/circuit_links/', link1));
                }
                if (link2) {
                    requests.push(backend.postRequest('services/circuit_links/', link2));
                }
                this.saveLinks(requests);
            } else if (this._props.pageType === 'edit') {
                let requests = [];
                if (this.oldLinks[0] && link1.location) {
                    requests.push(backend.putRequest('services/circuit_links/' + this.oldLinks[0].id, link1));
                } else if (!this.oldLinks[0] && link1.location) {
                    requests.push(backend.postRequest('services/circuit_links/', link1));
                }
                if (this.oldLinks[1] && link2.location) {
                    requests.push(backend.putRequest('services/circuit_links/' + this.oldLinks[1].id, link2));
                } else if (!this.oldLinks[1] && link2.location) {
                    requests.push(backend.postRequest('services/circuit_links/', link2));
                }
                this.saveLinks(requests);
            }
        },
        saveLinks(requests) {
            Promise.all(requests)
                .then(() => {
                    this.errorMessage = null;
                    this.$store.dispatch('addNotification', {
                        text: `Circuit links were saved successfully`,
                        variant: 'success'
                    });
                    this.isSaving = false;
                    if (this.pageType === 'add') {
                        this.clearForm();
                    }
                })
                .catch((error) => {
                    this.$store.dispatch('addNotification', {
                        text: error && error.response && error.response.data ||
                        `Something went wrong with links saving`,
                        variant: 'danger'
                    });
                });
        },
        loadLocations() {
            backend.getRequest('services/locations/')
                .then((response) => {
                    let formatter = new StringMask('$L000000', { reverse: true });
                    this.locations = response.data.results.map((item) => {
                        return {
                            value: item.id,
                            text: formatter.apply(item.number).toUpperCase() + ' | ' + item.name
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        loadDevices(pointId) {
            let locationId = this.form[pointId].location;
            if (!locationId) {
                return;
            }
            backend.getRequest('services/devices/?location=' + locationId)
                .then((response) => {
                    let formatter = new StringMask('D000000', { reverse: true });
                    this.devices[pointId] = response.data.results.map((item) => {
                        return {
                            value: item.id,
                            text: formatter.apply(item.number) + ' | ' + item.device_model + ' | ' + item.serial_number
                        };
                    });
                    this.devices = [].concat(this.devices);
                })
                .catch(this.onFetchingError);
        },
        onLocationChange(pointId) {
            setTimeout(() => {
                this.loadDevices(pointId);
                this.form[pointId].device = null;
            }, 100);
        },
        onFetchingError(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        clearForm() {
            this.form = {
                0: {
                    location: null,
                    device: null
                },
                1: {
                    location: null,
                    device: null
                }
            };
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
    .link-point {
        font-size: 17px;
        font-weight: 700;
        margin-bottom: 10px;
    }
    .link-point span {
        text-transform: uppercase;
    }
    .link-point i {
        font-size: 24px;
        color: #28a745;
    }
</style>
